<script>
import Table from '../../components/table/TableElement.vue'

export default ({
    name: "Patienten",
    components: {
        Table
    },
    data() {
        return {
            fetchUrl: `${this.$apiUrl}/CarePlan?opendis&selectPatient${this.$getUserId()}`
        }
    },
    beforeCreate() {
        this.$redirectNotAuthorizedToLogin([2])
        }
})
</script>


<template>
    <h1 class="my-5">Offene Aufgaben</h1>
    <div class="table-responsive">
        <Table :fetchLink="this.$apiUrl + '/CarePlan?opendis&selectPatient'"
            tableClasses="text-start table-striped" v-bind:tableColumns="
    [
        {
            name: 'ID',
            field: 'id',
            path: 'resource.id',
            align: 'text-start',
            weight: 'fw-light',
            type: 'patient'
        },
        {
            name: 'Vorname',
            field: 'vorname',
            path: 'resource.name[0].given[0]',
            align: 'text-start',
            color: 'text-primary',
            weight: 'fw-bold'
        },
        {
            name: 'Nachname',
            field: 'nachname',
            path: 'resource.name[0].family',
        },
        {
            name: 'Geschlecht',
            field: 'geschlecht',
            path: 'resource.gender',
            align: 'text-center',
            color: 'text-secondary',
        },
        {
            name: 'Geburtsdatum',
            field: 'geburtsdatum',
            path: 'resource.birthDate',
            color: 'text-secondary',

            isDate: true
        },
        {
            name: 'Telefonnummer',
            field: 'telefonnummer',
            path: 'resource.telecom[0].value',
            color: 'text-secondary',
        }
    ]" />
    </div>
</template>
